body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#root{
  height:100vh;
}

.create-question-container{

  display: inline-block;
  height:100%;
  width:100%;
  border-radius: 10px;
  box-shadow: 0 1px 15px rgba(0,0,0,0.12), 0 1px 16px rgba(0,0,0,0.24);
  background: white;
}
.create-sub-question-item-container {
  min-height:50px;
}
.create-question-item-container-box{
  display: inline-block;
  min-height:115px;
  width:100%;
  border-radius: 5px;
  box-shadow: 0 1px 5px rgba(0,0,0,0.12), 0 1px 6px rgba(0,0,0,0.24);
  background: white;
  margin-bottom:10px;
  padding:10px 20px 10px 10px;
}
.height-full{
  height:100%;
}
.padding-30{
  padding:17px;
}
.padding-40{
  padding:30px;
}
.add-question-button{
  width:45px;

}
.create-matrix-table{
  border-color: #000000 !important;
}
.create-matrix-table:active,
.create-matrix-table:focus,
.create-matrix-table:hover{
  border-color: #000000 !important;
}
.create-sub-question-input{
  border-width:0px 0px 0px 0px!important;
  border-radius: 0px !important;
  border-color: #13C64F !important;
  border-right-color: white !important;
}
.create-sub-question-input:active,
.create-sub-question-input:focus,
.create-sub-question-input:hover{
  border-width:0px 0px 0px 0px!important;
  border-radius: 0px !important;
  border-color: #13C64F !important;
  border-right-width: 0px !important !important;
  border-right-color: white !important;
  box-shadow: 0 0 0 0px rgba(29, 165, 122, 0.2) !important;
}
.create-question-input{
  border-width:0px 0px 0px 2px!important;
  border-radius: 0px !important;
  border-color: #13C64F !important;
  border-right-color: white !important;
}
.create-question-input:active,
.create-question-input:focus,
.create-question-input:hover{
  border-width:0px 0px 0px 2px!important;
  border-radius: 0px !important;
  border-color: #13C64F !important;
  border-right-width: 0px !important !important;
  border-right-color: white !important;
  box-shadow: 0 0 0 0px rgba(29, 165, 122, 0.2) !important;
}
.create-preview-input{
  border-width:0px 0px 2px 0px!important;
  border-radius: 0px !important;
  border-color: #FFFFFF !important;
  border-right-width: 0px !important !important;
  border-right-color: white !important;
  box-shadow: 0 0 0 0px rgba(29, 165, 122, 0.2) !important;
  background:  #13C64F !important;
  color:#FFFFFF !important;

}
.create-preview-input:active,
.create-preview-input:focus,
.create-preview-input:hover{
  border-width:0px 0px 2px 0px!important;
  border-radius: 0px !important;
  border-color: #FFFFFF !important;
  border-right-width: 0px !important !important;
  border-right-color: transparent !important;
  box-shadow: 0 0 0 0px rgba(29, 165, 122, 0.2) !important;
}
.create-preview-text-max{
  width:100%;
  text-align: right;
  color:#FFFFFF;
}
.second-row-container{

}
.create-preview-input-date > div  >.ant-calendar-picker-input {
  background: #13C64F !important;
  color:#FFFFFF !important;
  border-width:0px 0px 2px 0px!important;
  border-color: #FFFFFF !important;
}
.create-preview-input-date > div  > ::-webkit-input-placeholder {
  color: #FFFFFF;
  opacity: 1; /* Firefox */
}
.create-preview-input-date > div  > ::-ms-input-placeholder {
  color: #FFFFFF;
  opacity: 1; /* Firefox */
}
.create-preview-input-date > div  > ::placeholder {
  color: #FFFFFF;
  opacity: 1; /* Firefox */
}
.create-preview-input-date > div  >  .ant-calendar-picker-icon {
  color: #FFFFFF;
  opacity: 1; /* Firefox */
}
.create-preview-input-date > div  >  .ant-calendar-picker-clear {
  background: #13C64F !important;
}
.create-preview-drag-container > p{
  color:#ffffff ;
}
.create-preview-drag-container > .ant-upload.ant-upload-drag {
  border: 1px solid #ffffff !important;
}
.preview-upload-text {
  color:#ffffff !important;
}

.create-question-input-text-answer{

  border-width:0px 0px 0px 0px!important;
  border-radius: 0px !important;
  border-color: #13C64F !important;
  border-right-color: white !important;

}
.create-question-input-text-answer:active,
.create-question-input-text-answer:focus,
.create-question-input-text-answer:hover{
  border-width:0px 0px 0px 0px!important;
  border-radius: 0px !important;
  border-color: #13C64F !important;
  border-right-width: 0px !important !important;
  border-right-color: white !important;
  box-shadow: 0 0 0 0px rgba(29, 165, 122, 0.2) !important;
}
.badge-container{
  margin-top: 5px;
}

.preview-header{
  height:7%;
  border-top-right-radius:5px;
  border-top-left-radius:5px;

}
.preview-body{
  height:93%;
  border-bottom-right-radius:5px;
  border-bottom-left-radius:5px;

}
.ant-select-arrow{
  color:#13C64F !important;
}
.ant-select-selection__rendered{
  line-height: 1 !important;
  margin-top:7px ;

}
.ant-select-selection--single{
    height:35px !important;
}
.margin-top-30{
  margin-top:30px;
}
.margin-top-15{
  margin-top:15px;
}
.new-subgroup{
  margin-left: 30px;
  padding:5px;
}
.new-subgroup-box{
  padding:5px 5px;
  background: #13C64F ;
  color:white;
  text-align: center;
  border-radius: 5px;
  cursor:pointer;
}

